// 引入vue
import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './app';
import vheader from 'component/mui/Header';
import api from 'api';
import muiUtils from 'common/muiUtils';

Vue.use(VueRouter);
var router = new VueRouter();

// head
var HeadVue = Vue.extend(vheader);
new HeadVue({
    el: 'vheader',
    propsData: {
        title: '登录',
        statusbarcolor: '#fff',
        hdstyle: {
            back: {
                display: true
            },
            title: {
                display: true
            },
            rightButton: {
                display: true,
                text: '注册',
                onClick: function() {
                    muiUtils.openWindow(api.PAGE_URL.account_register.url, api.PAGE_URL.account_register.id);
                }
            }
        }
    }
});

mui.init({
    swipeBack: false
});
//mui生命周期管理
mui.plusReady(function() {
    router.start(app, '#app');
});